import { Component, OnInit } from '@angular/core';
import { NgForm } from '@angular/forms';
import { DialogBoxComponent } from '../dialog-box/dialog-box.component';
import { MatDialog } from '@angular/material';
import { BackendService } from '../backend.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-changepassword',
  templateUrl: './changepassword.component.html',
  styleUrls: ['./changepassword.component.css']
})
export class ChangepasswordComponent implements OnInit {
  userData ;
  constructor(

    private dialog:MatDialog,
    private backend:BackendService,
    private router : Router

  ) { }

  ngOnInit() {

    this.userData = this.backend.getUserFromLocalStorage();

  } //ngOnint ends here

  changePasswordSubmit(form:NgForm){
      if(form.value.newPass != form.value.confPass){
        let db = this.dialog.open(DialogBoxComponent,{
          data:{
            title:"Validation Error",
            message:"Password and Confirm Password not matching",
            type:'changePass',
            userid : "",
            icon : 'vpn_key'
          }
        })
    
        db.afterClosed().subscribe(()=>{
          //this.loadData();
        })
      }else{

          let data = {
            userId   : this.userData.userId,
            oldPass  : form.value.oldPass,
            password : form.value.newPass
          }
          console.log(data);
          this.backend.changePassword(data)
          .subscribe((response)=>{
             if(response.status == 200){
              let db = this.dialog.open(DialogBoxComponent,{
                data:{
                  title:"Success",
                  message:"Password Updated,Please Login with new Passsword",
                  type:'passUpdated',
                  userid : "",
                  icon : 'published_with_changes'
                }
              })
          
              db.afterClosed().subscribe(()=>{
                this.backend.logout();
                this.router.navigate(['/home-dash']);
              })
             }else{
              let db = this.dialog.open(DialogBoxComponent,{
                data:{
                  title:"Error",
                  type:'passUpdated',
                  message:"Old Password not matching",
                  userid : "",
                  icon : 'error'
                }
              })
             }
          })

      }
  }

}//Main class ends here
