//Get Level Count
getLevelCount(uid){
    let arr = this.loadUserData(uid);
    console.log("TREE ARRAY ",arr);
    let countObj = {} 
     let level = 0;
  
      for(let dt of arr){
        console.log(dt);
        if(dt.parentid !== 0){
         // console.log(countObj.hasOwnProperty(dt.parentid));
          countObj[dt.parentid] = (countObj[dt.parentid] || 0) + 1;
        } 
      }
  
      for(let key in countObj){
        if(countObj[key] == Math.pow(2,Number(key))){
          level++;
        }
      }
  
    return level;
  }//getLevelCount ends here 




